body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #514e4e;
}

.Portfolio {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: -webkit-min-content;
  grid-template-rows: min-content;
  font-family: 'Raleway', sans-serif;
  min-height: 100vh;
}

@media only screen and (min-width: 768px) {
  .Portfolio {
    grid-template-columns: 250px 1fr;
    grid-template-rows: -webkit-min-content 1fr;
    grid-template-rows: min-content 1fr;
  }
}

a {
  color: #4c3b4d;
}

.contact {
  background: #4c3b4d;
  color: #c1bdb3;
  font-size: 1.25em;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  padding: 10px;
  height: 100%;
  width: 100%;
}

.contact-item {
  height: 35px;
  width: 35px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  margin: 0 auto;
  border-radius: 50%;
  background: #c1bdb3;
}

.emptySpace {
  display: none;
}

@media only screen and (min-width: 768px) {
  .contact {
    font-size: 1.5em;
  }

  .contact-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .contact-item {
    height: 50px;
    width: 50px;
  }

  .emptySpace {
    display: block;
  }
}

.header {
  background: #4c3b4d;
  color: #c1bdb3;
}

.header-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.header-item {
  margin: 0 auto;
}

h1 {
  font-size: 2em;
}

h3 {
  font-size: 1.25em;
  line-height: 1.25em;
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 3em;
  }

  h3 {
    font-size: 1.5em;
    line-height: 1.5em;
  }

}


.nav-icon {
  display: block;
  background: #4c3b4d;
  color: #c1bdb3;
  font-size: 1.5em;
  text-align: center;
  padding: 5px;
}

.nav-icon:hover, .navbar:hover {
  cursor: pointer;
}

.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: stretch;
          justify-content: stretch;
  font-size: 1.5em;
  transition: height .5s 0s ease-in-out;
}

.navbar.off {
  height: 0;
  overflow: hidden;
}

.navbar.on {
  height: 100%;
  overflow: hidden;
}

@media only screen and (min-width: 768px) {
  .nav-icon {
    display: none;
  }

  .navbar {
    display: -webkit-flex !important;
    display: flex !important;
    height: 100% !important;
  }
}

.nav-item {
  text-align: center;
  padding: 10px 0;
  border: solid #000000 1px;
  background: #edc79b;
  color: #000000;
  font-family: inherit;
}

.active-item {
  background: #bdc696;
  display: block;
}

.project-link a {
  color: #edc79b;
}

.profilePic {
  float: left;
  -webkit-shape-outside: circle(50%);
          shape-outside: circle(50%);
}

.about-img{
  border-radius: 50%;
  margin: 10px;
  width: 200px;
  height: 200px;
}

.display {
  background: #514e4e;
  color: #c1bdb3;
  font-size: 1.25em;
}

.display-about, .project-description {
  text-align: right;
  width: 90%;
  margin: 10px auto;
  line-height: 1.1em;
}

.project-description {
  display: grid;
  text-align: justify;
}

.display-project {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  text-align: center;
  width: 100%;
}

.project-name {
  font-size: 1.5em;
  margin: 10px auto;
  width: 90%;
}

.project-thumbnail {
  -webkit-align-self: center;
          align-self: center;
  width: 100%;
  
  display: -webkit-flex;
  
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

@media (min-width: 768px) {
  .display {
    font-size: 1.5em;
  }

  .about-img {
    width: 400px;
    height: 400px;
  }

  .display-project {
    grid-template-columns: repeat(2, 1fr);
  }

  .project-name {
    font-size: 2em;
    grid-column: 1 / 3;
  }

  .project-thumbnail {
    grid-column: 1 / 3;
    height: 100%;
  }

  .project-description {
    grid-column: 1 / 3;
  }
}

